import React, { useEffect, useState } from "react";
import { useContext } from "react";
import { Alert } from "react-bootstrap";
import { BrowserRouter, Switch, Route } from "react-router-dom";
import Footer from "./Footer";
import Header from "./Header";
import HomePage from "./HomePage";
import Page from "./Page";
// import '../styles/bootstrap-dark.css';

export const ThemeContext = React.createContext({theme: "dark", setTheme: null});

export default function Main() {
  const [theme, setTheme] = useState("dark")
  const coursesTextClass = theme === "dark"? "text-secondary fst-italic":"text-primary fst-italic"
  // var head = document.head;
  // var link = document.createElement("link");

  // link.type = "text/css";
  // link.rel = "stylesheet";
  // link.href = "../src/styles/bootstrap-dark.min.css";

  // head.appendChild(link);

  // useEffect(() => {
  //   var head = document.head;
  //   var link = document.createElement("link");

  //   link.type = "text/css";
  //   link.rel = "stylesheet";
  //   link.href = "../styles/bootstrap-dark.min.css";

  //   head.appendChild(link);

  //   return () => { head.removeChild(link); }

  // });
  // if(theme == "dark") require('../styles/bootstrap-dark.min.css')
  // else require('../styles/bootstrap-light.min.css')

    return(
      <ThemeContext.Provider value={{theme: theme, setTheme: setTheme}} >
        <link rel="stylesheet" type="text/css" href={'../styles/bootstrap-dark.min.css'} />
        <div className='mainContainer'>
        <BrowserRouter basename="/ASUENG-Library">
          <Header />
          <Alert variant={theme == "dark"? "success" : "info"} className="mt-3 mb-0">
              Currently available courses: (Electrical: <strong className={coursesTextClass}>Electrical Circuits 1</strong>, <strong className={coursesTextClass}>Logic Design</strong>, <strong className={coursesTextClass}>Modern Physics</strong>) , (All Departments: <strong className={coursesTextClass}>Probability n Statistics</strong>, <strong className={coursesTextClass}>Report Writing</strong>)
          </Alert>
          <Switch>
            <Route exact path='/'>
              <HomePage />
            </Route>
            <Route path='/'>
              <Page />
            </Route>
          </Switch>
          <Footer />
        </BrowserRouter>
        </div>
      </ThemeContext.Provider>
    )
}